import "./App.css";
import TileContainer from "./components/tile-container.component";
import IntroductionModal from "./components/introduction-modal.component";
import SearchBar from "./components/search-bar.component";
import React, { useReducer, useEffect } from "react";
import { AppContext } from "./context";
import styled from "styled-components";
import curtains from "../src/curtains.jpeg";
import { Fragment } from "react/cjs/react.production.min";

const reducer = (state, action) => {
  switch (action.type) {
    case "LIKE":
      return { ...state, likes: [...state.likes, action.payload] };
    case "DISLIKE":
      return { ...state, dislikes: [...state.dislikes, action.payload] };
    case "FILM":
      return { ...state, films: [...state.films, ...action.payload] };
    case "TOGGLEMODAL":
      return { ...state, modal: !state.modal };
    case "REMOVEFILM":
      return {
        ...state,
        films: state.films.reduce((memo, iter) => {
          return iter.id !== action.payload ? memo.concat(iter) : memo;
        }, []),
      };
    case "SEARCH":
      return { ...state, searchItems: action.payload };
  }
};

const initialState = {
  likes: [],
  dislikes: [],
  films: [],
  modal: true,
  searchItems: [],
  recommendations: undefined,
};

const Curtains = styled.div`
  background-image: url(${curtains});
  background-size: cover;
  height: 100%;
  width: 100%;
  z-index: 1;
  position: absolute;
  transition: 2s;
  ${(props) =>
    !props.open ? `transform: translateX(` + props.direction + "%)" : null}
`;

function App() {
  const [state, dispatch] = useReducer(reducer, initialState);

  useEffect(() => {
    fetch(`http://localhost:3000/frequent`)
      .then((response) => response.json())
      .then((response) => {
        console.log(response);
        dispatch({
          type: "FILM",
          payload: response !== undefined ? response : [],
        });
      })
      .catch((err) => {
        console.log(err);
      });
  }, []);
  return (
    <div className="App">
      <AppContext.Provider value={{ dispatch }}>
        {state.recommendations === undefined ? (
          <Fragment>
            <Curtains direction={"-100"} open={state.modal} />
            <Curtains direction={"+100"} open={state.modal} />
            {/* <Curtains direction={"right"} open={!state.modal} /> */}

            <SearchBar />
            {state.modal ? <IntroductionModal></IntroductionModal> : null}
            <TileContainer
              films={
                state.searchItems.length > 0 ? state.searchItems : state.films
              }
              modalOpen={state.modal}
            ></TileContainer>
          </Fragment>
        ) : null}
      </AppContext.Provider>
    </div>
  );
}

export default App;
